import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "KindredCircl: Discover, Connect, Belong"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.kindredcircl.web.pages.homePage() }
        ctx.router.register("/about") { com.kindredcircl.web.pages.aboutPage() }
        ctx.router.register("/beta") { com.kindredcircl.web.pages.betaPage() }
        ctx.router.register("/guidelines") { com.kindredcircl.web.pages.guidelinesPage() }
        ctx.router.register("/privacy") { com.kindredcircl.web.pages.privacyPage() }
        ctx.router.register("/tos") { com.kindredcircl.web.pages.tosPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("body", com.kindredcircl.web.BodyStyle)
        ctx.theme.registerStyle("hero-container", com.kindredcircl.web.HeroContainerStyle)
        ctx.theme.registerStyle("div-container", com.kindredcircl.web.DivContainerStyle)
        ctx.theme.registerStyle("paragraph-text", com.kindredcircl.web.ParagraphTextStyle)
        ctx.theme.registerStyle("bullet-list", com.kindredcircl.web.BulletListStyle)
        ctx.theme.registerStyle("headline1-text", com.kindredcircl.web.Headline1TextStyle)
        ctx.theme.registerStyle("headline2-text", com.kindredcircl.web.Headline2TextStyle)
        ctx.theme.registerStyle("headline3-text", com.kindredcircl.web.Headline3TextStyle)
        ctx.theme.registerStyle("headline4-text", com.kindredcircl.web.Headline4TextStyle)
        ctx.theme.registerStyle("headline5-text", com.kindredcircl.web.Headline5TextStyle)
        ctx.theme.registerStyle("prohibited-headlines",
                com.kindredcircl.web.ProhibitedHeadlinesStyle)
        ctx.theme.registerStyle("positive-headlines", com.kindredcircl.web.PositiveHeadlinesStyle)
        ctx.theme.registerStyle("menu-drawer", com.kindredcircl.web.MenuDrawerStyle)
        ctx.theme.registerStyle("menu-header", com.kindredcircl.web.MenuHeaderStyle)
        ctx.theme.registerStyle("button", com.kindredcircl.web.ButtonStyle)
        ctx.theme.registerStyle("back-to-top-button", com.kindredcircl.web.BackToTopButtonStyle)
        ctx.theme.registerStyle("link-base", com.kindredcircl.web.LinkBaseStyle)
        ctx.theme.registerStyle("copyright", com.kindredcircl.web.CopyrightStyle)
        ctx.theme.registerStyle("social-link", com.kindredcircl.web.SocialLinkStyle)
        ctx.theme.registerStyle("footer", com.kindredcircl.web.FooterStyle)
        ctx.theme.registerStyle("blockquote", com.kindredcircl.web.BlockquoteStyle)
        ctx.theme.registerStyle("blockquote-footer", com.kindredcircl.web.BlockquoteFooterStyle)
        ctx.theme.registerStyle("kindred-circl", com.kindredcircl.web.KindredCirclStyle)
        ctx.theme.registerStyle("toc-inline-container",
                com.kindredcircl.web.TocInlineContainerStyle)
        ctx.theme.registerStyle("toc-link-text", com.kindredcircl.web.TocLinkTextStyle)
        ctx.theme.registerStyle("expandable-toc", com.kindredcircl.web.ExpandableTocStyle)
        ctx.theme.registerStyle("toc-sub-list", com.kindredcircl.web.TocSubListStyle)
        ctx.theme.registerStyle("markdown", com.kindredcircl.web.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content",
                com.kindredcircl.web.components.layouts.PageContentStyle)
        ctx.theme.registerVariant("-circle", com.kindredcircl.web.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", com.kindredcircl.web.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("expand-sub-list", com.kindredcircl.web.ExpandSubListAnim)
        ctx.theme.registerKeyframes("expand-collapse", com.kindredcircl.web.ExpandCollapseAnim)
        ctx.theme.registerKeyframes("menu-slide-in",
                com.kindredcircl.web.components.sections.MenuSlideInAnim)
        ctx.theme.registerKeyframes("exapnd-collapse",
                com.kindredcircl.web.components.widgets.ExapndCollapseAnim)
        com.kindredcircl.web.initColorMode(ctx)
        com.kindredcircl.web.initSiteStyles(ctx)
        com.kindredcircl.web.initTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        com.kindredcircl.web.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
